@-webkit-keyframes myfirst{0%{background-position:0    0, 100%   100%, 100% 0,     0 100%}100%{background-position:0 +100px, 100%   0%,     0 0,  100% 100%}}.background{width:100px;height:100px;padding:5px}.background{-webkit-animation:myfirst 3s;-webkit-animation-timing-function:linear;-webkit-animation-iteration-count:infinite;background-color:transparent;background-image:linear-gradient(0deg, transparent, transparent 50%, blue 50%, blue 100%),linear-gradient(0deg, transparent, transparent, transparent 50%, blue 50%, blue 100%),linear-gradient(90deg, transparent, transparent 50%, blue 50%, blue 100%),linear-gradient(90deg, transparent, transparent 50%, blue 50%, blue 100%);background-repeat:repeat-y,  repeat-y,  repeat-x,  repeat-x;background-size:2px 20px, 2px 20px, 20px 2px, 20px 2px;opacity:0.7}
